// Mixins for common actions:
@mixin flex-centered {
    display:flex;
    justify-content: center;
    align-items: center;
}

@mixin title-box-cent {
    height: $title-box-height;

    display: flex;
    align-items: center;
}

@mixin transform-center{
    transform: translate(-50%, 50%);
}


// AUX
@mixin center-button{
    left: 50%;
    position: absolute;

    transform: translate(-50%,-50%)
}